import { css } from 'lit';

export const styles = css`:host {
  --background: var(--ig-list-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --header-background: var(--ig-list-header-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --header-text-color: var(--ig-list-header-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --item-background: var(--ig-list-item-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --item-background-hover: var(--ig-list-item-background-hover, hsla(var(--ig-primary-50), var(--ig-primary-a)));
  --item-background-active: var(--ig-list-item-background-active, hsla(var(--ig-primary-50), var(--ig-primary-a)));
  --item-text-color: var(--ig-list-item-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-text-color-hover: var(--ig-list-item-text-color-hover, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-text-color-active: var(--ig-list-item-text-color-active, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-title-color: var(--ig-list-item-title-color, 'currentColor');
  --item-title-color-hover: var(--ig-list-item-title-color-hover, 'currentColor');
  --item-title-color-active: var(--ig-list-item-title-color-active, 'currentColor');
  --item-action-color: var(--ig-list-item-action-color, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --item-action-color-hover: var(--ig-list-item-action-color-hover, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --item-action-color-active: var(--ig-list-item-action-color-active, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --item-subtitle-color: var(--ig-list-item-subtitle-color, hsla(var(--ig-gray-800), var(--ig-gray-a)));
  --item-subtitle-color-hover: var(--ig-list-item-subtitle-color-hover, hsla(var(--ig-gray-800), var(--ig-gray-a)));
  --item-subtitle-color-active: var(--ig-list-item-subtitle-color-active, hsla(var(--ig-gray-800), var(--ig-gray-a)));
  --item-thumbnail-color: var(--ig-list-item-thumbnail-color, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --item-thumbnail-color-hover: var(--ig-list-item-thumbnail-color-hover, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --item-thumbnail-color-active: var(--ig-list-item-thumbnail-color-active, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --border-color: var(--ig-list-border-color, transparent);
  --border-width: var(--ig-list-border-width, 0);
  --border-radius: var(--ig-list-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0) * 1.5rem), 1.5rem));
  --item-border-radius: var(--ig-list-item-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.0833333333) * 1.5rem), 1.5rem));
  color: var(--item-text-color);
  background: var(--item-background);
  border-radius: var(--item-border-radius);
}

:host(:hover),
:host(:focus-within) {
  background: var(--item-background-hover);
}

::slotted(igc-icon) {
  color: var(--item-action-color);
}

[part=title] {
  color: var(--item-title-color);
}

[part=subtitle] {
  font-family: var(--ig-body-2-font-family, var(--ig-font-family));
  font-size: var(--ig-body-2-font-size);
  font-weight: var(--ig-body-2-font-weight);
  font-style: var(--ig-body-2-font-style);
  line-height: var(--ig-body-2-line-height);
  letter-spacing: var(--ig-body-2-letter-spacing);
  text-transform: var(--ig-body-2-text-transform);
  margin-top: var(--ig-body-2-margin-top);
  margin-bottom: var(--ig-body-2-margin-bottom);
  color: var(--item-subtitle-color);
}`;
